h3,
p {
  margin: 0;
  padding: 0;
}
.action-menu-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #222327;
}
.list {
  display: flex;
  gap: 8px;
}
.list .user {
  position: relative;
  background: #fff;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  padding: 10px;
  gap: 10px;
}
.list .user .imgBox {
  position: relative;
  width: 50px;
  height: 50px;
  overflow: hidden;
  border-radius: 6px;
}
.list .user .imgBox img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.list .user .details {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.list .user .details h3 {
  font-weight: 600;
  line-height: 1.1em;
}
.list .user .details p {
  font-weight: 500;
  font-size: 0.8em;
  color: rgba(0, 0, 0, 0.8);
}
.navigation {
  position: relative;
  width: 40px;
  height: 70px;
  background: #fff;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.navigation.active {
  width: 200px;
}
.navigation span {
  position: absolute;
  width: 6px;
  height: 6px;
  background: #222327;
  border-radius: 50%;
  transition: 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid#222327;
}
.navigation span:nth-child(1) {
  transform: translateY(-12px);
}
.navigation span:nth-child(3) {
  transform: translateY(12px);
}
.navigation.active span {
  width: 50px;
  height: 50px;
  transition: 0.5s;
}
/* .navigation .active span:nth-child(1),
.navigation .active span:nth-child(3) {
  transform: translateY(0);
} */
.navigation.active span:nth-child(1) {
  transform: translateY(0) translateX(-60px);
}
.navigation.active span:nth-child(3) {
  transform: translateY(0) translateX(60px);
}
.navigation span i {
  /* color: #fff; */
  transition: 0.5s;
  font-size: 0em;
}
.navigation.active span i {
  font-size: 1.25em;
  color: #fff;
}
.navigation.active span:hover {
  background: #29fd53;
}
.navigation.active span:hover i {
  color: #222327;
}
